<template>
  <div class="hello">
    <h2>2019年度还贷明细</h2>
    <div class="chart">
      <el-form
        ref="form"
        label-width="80px"
      >
        <el-form-item label="月份">
          <el-select
            v-model="selectTime"
            placeholder="请选择"
            @change="drawEcharts1"
          >
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            >
            </el-option>
          </el-select>
        </el-form-item>
      </el-form>

      <div
        id="echarts"
        :style="{width: '800px', height: '500px'}"
      ></div>
    </div>
    <el-divider></el-divider>
    <div
      class="chart"
      id="echarts2"
      :style="{width: '1000px', height: '500px'}"
    ></div>
  </div>
</template>
<script>
var echarts = require("echarts");
import data from "../assets/data";
export default {
  name: "HelloWorld",
  props: {},
  watch: {
    selectTime(val) {}
  },
  data() {
    return {
      options: [],
      selectTime: data[0].time,
      typeList: {
        all: "总费用",
        fang: "房贷",
        car: "车贷",
        zs: "招商信用卡",
        jb: "我的借呗",
        wifejb: "老婆借呗",
        hb: "我的花呗",
        jh: "交通信用卡",
        gjd: "公鸡贷"
      }
    };
  },
  methods: {
    drawEcharts1() {
      let dom = document.getElementById("echarts");
      let myChart = this.echarts.init(dom);
      let monthData = data
        .filter(item => item.time === this.selectTime)[0]
        .data.map(item => ({
          name: this.typeList[Object.keys(item)[0]],
          value: Object.values(item)[0]
        }));

      myChart.setOption({
        series: [
          {
            name: "",
            type: "pie",
            radius: "55%",
            data: monthData,
            itemStyle: {
              normal: {
                label: {
                  show: true,
                  formatter: "{b} : {c} ({d}%)"
                },
                labelLine: { show: true }
              }
            }
          }
        ],
        tooltip: {
          trigger: "item",
          formatter: "{a} <br/>{b}"
        }
      });
    },
    drawEcharts2() {
      let dom2 = document.getElementById("echarts2");
      let myChart2 = this.echarts.init(dom2);
      let series = Object.keys(this.typeList).map(item => {
        if (item !== "all") {
          return {
            name: this.typeList[item],
            type: "line",
            smooth: false,
            data: data.reduce((t, c) => {
              let k = c.data.filter(i => Object.keys(i)[0] === item)[0][item];
              t.push(k);
              return t;
            }, [])
          };
        } else {
          return {
            name: this.typeList[item],
            type: "line",
            smooth: false,
            data: data.reduce((t, c) => {
              let k = c.data
                .map(i => Object.values(i)[0])
                .reduce((o, p) => o + p, 0);
              t.push(k);
              return t;
            }, [])
          };
        }
      });

      myChart2.setOption({
        title: {
          text: "每月还贷明细",
          subtext: ""
        },
        tooltip: {
          trigger: "axis"
        },
        legend: {},
        toolbox: {
          show: true,
          feature: {
            magicType: { show: true, type: ["stack", "tiled"] },
            saveAsImage: { show: true }
          }
        },
        xAxis: {
          type: "category",
          boundaryGap: false,
          data: data.map(i => i.time)
        },
        yAxis: {
          type: "value"
        },
        series
      });
    }
  },
  mounted() {
    this.options = data.map(item => ({
      value: item.time,
      label: item.time
    }));
    this.drawEcharts1();
    this.drawEcharts2();
  }
};
</script>
<style scoped>
.chart {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto;
}
</style>